// ============================================
// SEARCH OVERLAY STYLES
// Global search overlay component
// ============================================

@use "../../../scss/abstracts" as *;

// ============================================
// ============================================
// SEARCH OVERLAY - Base
// ============================================
// ============================================

.#{$so-prefix}search-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: z-index("modal");
  display: none;

  &.#{$so-prefix}active {
    display: flex;
    flex-direction: column;
  }
}

.#{$so-prefix}search-overlay-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);

  @include dark-mode {
    background: rgba(0, 0, 0, 0.6);
  }
}

.#{$so-prefix}search-overlay-content {
  position: relative;
  width: 100%;
  height: 100%;
  max-height: 100%;
  background: css-var("card-bg");
  border-radius: 0;
  box-shadow: none;
  border: none;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  animation: so-search-fade-in 0.15s ease-out;

  @include dark-mode {
    box-shadow: none;
  }
}

@keyframes so-search-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

// ============================================
// SEARCH HEADER
// ============================================

.#{$so-prefix}search-overlay-header {
  padding: spacing("5") spacing("6");
  border-bottom: 1px solid css-var("border-color");
  flex-shrink: 0;
}

.#{$so-prefix}search-overlay-input-wrapper {
  display: flex;
  align-items: center;
  gap: spacing("3");
}

.#{$so-prefix}search-overlay-icon {
  font-size: 24px;
  color: css-var("text-secondary");
}

.#{$so-prefix}search-overlay-input {
  flex: 1;
  border: none;
  outline: none;
  font-size: font-size("lg");
  font-family: inherit;
  color: css-var("text-heading");
  background: transparent;

  &::placeholder {
    color: css-var("text-muted");
  }
}

.#{$so-prefix}search-overlay-shortcut {
  display: flex;
  gap: spacing("1");
}

.#{$so-prefix}search-overlay-shortcut kbd,
.#{$so-prefix}search-overlay-footer-hint kbd {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 24px;
  height: 24px;
  padding: 0 spacing("1") + 2px;
  font-size: 11px;
  font-family: inherit;
  font-weight: font-weight("medium");
  color: css-var("text-secondary");
  background: css-var("card-hover-bg");
  border: 1px solid css-var("border-color");
  border-radius: radius("base");

  @include dark-mode {
    border-color: #474d64;
  }
}

// Close Button
.#{$so-prefix}search-overlay-close {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  margin-left: spacing("2");
  background: transparent;
  border: none;
  border-radius: radius("md");
  color: css-var("text-secondary");
  cursor: pointer;
  transition: all $so-transition-base;

  &:hover {
    background: css-var("card-hover-bg");
    color: css-var("accent-danger");
  }

  .material-icons {
    font-size: 22px;
  }
}

// ============================================
// SEARCH BODY & FOOTER
// ============================================

.#{$so-prefix}search-overlay-body {
  flex: 1;
  overflow-y: auto;
}

.#{$so-prefix}search-overlay-footer {
  padding: spacing("3") spacing("6");
  border-top: 1px solid css-var("border-color");
  background: css-var("page-bg");
  flex-shrink: 0;
}

.#{$so-prefix}search-overlay-footer-hint {
  display: flex;
  align-items: center;
  gap: spacing("2");
  font-size: font-size("xs");
  color: css-var("text-secondary");

  kbd {
    margin-right: spacing("1");
  }
}

// ============================================
// QUICK LINKS
// ============================================

.#{$so-prefix}search-quick-links {
  padding: spacing("5") spacing("6");
  border-bottom: 1px solid css-var("border-color");
}

.#{$so-prefix}search-quick-links-title {
  font-size: font-size("xs");
  font-weight: font-weight("semibold");
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: css-var("text-muted");
  margin-bottom: spacing("4");
}

.#{$so-prefix}search-quick-links-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: spacing("3");
}

.#{$so-prefix}search-quick-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: spacing("2");
  padding: spacing("4") spacing("2");
  border-radius: radius("lg");
  text-decoration: none;
  color: css-var("text-primary");
  background: css-var("card-hover-bg");
  transition: all $so-transition-base;

  &:hover {
    background: css-var("accent-primary");
    color: $so-white;
    text-decoration: none;

    .#{$so-prefix}search-quick-link-icon {
      background: rgba(255, 255, 255, 0.2);
      color: $so-white;
    }
  }
}

.#{$so-prefix}search-quick-link-icon {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: radius("full");
  background: css-var("card-bg");
  transition: all $so-transition-base;

  // Color variants
  &.blue {
    background: #e8f0fe;
    color: $so-primary;
  }
  &.green {
    background: #e6f4ea;
    color: $so-success;
  }
  &.orange {
    background: #fef7e0;
    color: $so-warning;
  }
  &.red {
    background: #fce8e6;
    color: $so-danger;
  }
  &.purple {
    background: #f3e8fd;
    color: #9334ea;
  }
  &.teal {
    background: #e0f7fa;
    color: #00897b;
  }

  @include dark-mode {
    &.blue {
      background: rgba(115, 103, 240, 0.15);
      color: css-var("accent-primary");
    }
    &.green {
      background: rgba(40, 199, 111, 0.15);
      color: css-var("accent-success");
    }
    &.orange {
      background: rgba(255, 159, 67, 0.15);
      color: css-var("accent-warning");
    }
    &.red {
      background: rgba(234, 84, 85, 0.15);
      color: css-var("accent-danger");
    }
    &.purple {
      background: rgba(147, 51, 234, 0.15);
      color: #a855f7;
    }
    &.teal {
      background: rgba(0, 137, 123, 0.15);
      color: #2dd4bf;
    }
  }
}

.#{$so-prefix}search-quick-link-text {
  font-size: font-size("xs");
  font-weight: font-weight("medium");
  text-align: center;
}

// ============================================
// FILTER BAR
// ============================================

.#{$so-prefix}search-filter-bar {
  display: none;
  align-items: center;
  gap: spacing("3");
  padding: spacing("3") spacing("6");
  border-bottom: 1px solid css-var("border-color");
  flex-wrap: wrap;

  &.#{$so-prefix}visible {
    display: flex;
  }
}

.#{$so-prefix}search-filter-dropdown {
  position: relative;
}

.#{$so-prefix}search-filter-btn {
  display: flex;
  align-items: center;
  gap: spacing("1") + 2px;
  padding: spacing("1") + 2px spacing("3");
  font-size: font-size("sm");
  font-family: inherit;
  color: css-var("text-secondary");
  background: css-var("card-hover-bg");
  border: 1px solid css-var("border-color");
  border-radius: radius("md");
  cursor: pointer;
  transition: all $so-transition-base;

  &:hover,
  &.#{$so-prefix}active {
    border-color: css-var("accent-primary");
    color: css-var("accent-primary");
  }

  .material-icons {
    font-size: 18px;
  }
}

.#{$so-prefix}search-filter-menu {
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: spacing("1");
  min-width: 160px;
  background: css-var("card-bg");
  border: 1px solid css-var("border-color");
  border-radius: radius("lg");
  box-shadow: css-var("shadow-lg");
  z-index: z-index("dropdown") + 10;
  display: none;

  &.#{$so-prefix}open {
    display: block;
  }
}

.#{$so-prefix}search-filter-option {
  display: flex;
  align-items: center;
  gap: spacing("2");
  padding: spacing("2") + 2px spacing("3");
  font-size: font-size("sm");
  color: css-var("text-primary");
  cursor: pointer;
  transition: background $so-transition-base;

  &:first-child {
    border-radius: radius("lg") radius("lg") 0 0;
  }

  &:last-child {
    border-radius: 0 0 radius("lg") radius("lg");
  }

  &:hover {
    background: css-var("card-hover-bg");
  }

  &.#{$so-prefix}selected {
    color: css-var("accent-primary");
  }

  .check-icon {
    display: none;
    font-size: 18px;
  }

  &.selected .check-icon {
    display: inline-flex;
  }
}

// ============================================
// CATEGORY TABS
// ============================================

.#{$so-prefix}search-category-tabs {
  display: none;
  gap: spacing("1");
  padding: spacing("3") spacing("6");
  border-bottom: 1px solid css-var("border-color");
  overflow-x: auto;

  &.#{$so-prefix}visible {
    display: flex;
  }
}

.#{$so-prefix}search-category-tab {
  padding: spacing("2") spacing("4");
  font-size: font-size("sm");
  font-weight: font-weight("medium");
  font-family: inherit;
  color: css-var("text-secondary");
  background: transparent;
  border: none;
  border-radius: radius("md");
  cursor: pointer;
  white-space: nowrap;
  transition: all $so-transition-base;

  &:hover {
    background: css-var("card-hover-bg");
  }

  &.#{$so-prefix}active {
    background: css-var("accent-primary");
    color: $so-white;
  }
}

.#{$so-prefix}search-category-count {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 20px;
  height: 20px;
  padding: 0 spacing("1") + 2px;
  margin-left: spacing("1") + 2px;
  font-size: 11px;
  font-weight: font-weight("semibold");
  background: rgba(0, 0, 0, 0.1);
  border-radius: 10px;

  .#{$so-prefix}search-category-tab.active & {
    background: rgba(255, 255, 255, 0.2);
  }
}

// ============================================
// VIEW TOGGLE
// ============================================

.#{$so-prefix}search-view-toggle {
  display: flex;
  margin-left: auto;
}

.#{$so-prefix}search-view-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background: transparent;
  border: 1px solid css-var("border-color");
  color: css-var("text-secondary");
  cursor: pointer;
  transition: all $so-transition-base;

  &:first-child {
    border-radius: radius("md") 0 0 radius("md");
  }

  &:last-child {
    border-radius: 0 radius("md") radius("md") 0;
    border-left: none;
  }

  &:hover {
    background: css-var("card-hover-bg");
  }

  &.#{$so-prefix}active {
    background: css-var("accent-primary");
    border-color: css-var("accent-primary");
    color: $so-white;
  }
}

// ============================================
// RESULTS CONTAINER
// ============================================

.#{$so-prefix}search-results-container {
  flex: 1;
  overflow-y: auto;
}

// Results Grid View
.#{$so-prefix}search-results-grid {
  display: none;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: spacing("4");
  padding: spacing("5") spacing("6");

  &.#{$so-prefix}visible {
    display: grid;
  }
}

// Results List View
.#{$so-prefix}search-results-list {
  display: none;
  flex-direction: column;
  padding: spacing("3") spacing("6");

  &.#{$so-prefix}visible {
    display: flex;
  }
}

// List Header
.#{$so-prefix}search-list-header {
  display: grid;
  grid-template-columns: 1fr 80px 80px 80px 80px;
  gap: spacing("4");
  padding: spacing("2") 0;
  font-size: 11px;
  font-weight: font-weight("semibold");
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: css-var("text-muted");
  border-bottom: 1px solid css-var("border-color");

  span:not(:first-child) {
    text-align: right;
  }
}

// ============================================
// ITEM CARD (Grid View)
// ============================================

.#{$so-prefix}search-item-card {
  background: css-var("card-bg");
  border: 1px solid css-var("border-color");
  border-radius: radius("lg");
  padding: spacing("4");
  cursor: pointer;
  transition: all $so-transition-base;

  &:hover {
    border-color: css-var("accent-primary");
    box-shadow: css-var("shadow");
  }
}

.#{$so-prefix}search-item-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: spacing("3");
}

.#{$so-prefix}search-item-card-sku {
  font-size: 11px;
  font-weight: font-weight("semibold");
  color: css-var("accent-primary");
  text-transform: uppercase;
}

.#{$so-prefix}search-item-card-status {
  padding: 2px spacing("2");
  font-size: 10px;
  font-weight: font-weight("semibold");
  text-transform: uppercase;
  border-radius: radius("base");

  &.#{$so-prefix}active {
    background: rgba(40, 199, 111, 0.15);
    color: css-var("accent-success");
  }

  &.liquidation {
    background: rgba(255, 159, 67, 0.15);
    color: css-var("accent-warning");
  }
}

.#{$so-prefix}search-item-card-title {
  font-size: font-size("base");
  font-weight: font-weight("medium");
  color: css-var("text-heading");
  margin-bottom: spacing("2");
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.#{$so-prefix}search-item-card-details {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: spacing("2");
  margin-top: spacing("3");
  padding-top: spacing("3");
  border-top: 1px solid css-var("border-color");
}

.#{$so-prefix}search-item-card-detail {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.#{$so-prefix}search-item-card-detail-label {
  font-size: 10px;
  font-weight: font-weight("medium");
  text-transform: uppercase;
  color: css-var("text-muted");
}

.#{$so-prefix}search-item-card-detail-value {
  font-size: font-size("sm");
  font-weight: font-weight("medium");
  color: css-var("text-primary");

  &.in-stock {
    color: css-var("accent-success");
  }
  &.low-stock {
    color: css-var("accent-warning");
  }
  &.out-of-stock {
    color: css-var("accent-danger");
  }
}

// ============================================
// ITEM ROW (List View)
// ============================================

.#{$so-prefix}search-item-row {
  display: grid;
  grid-template-columns: 1fr 80px 80px 80px 80px;
  gap: spacing("4");
  align-items: center;
  padding: spacing("3") 0;
  border-bottom: 1px solid css-var("border-color");
  cursor: pointer;
  transition: background $so-transition-base;

  &:hover {
    background: css-var("card-hover-bg");
    margin: 0 spacing("6") * -1;
    padding: spacing("3") spacing("6");
  }
}

.#{$so-prefix}search-item-row-info {
  display: flex;
  flex-direction: column;
  gap: spacing("1");
}

.#{$so-prefix}search-item-row-title {
  font-size: font-size("base");
  font-weight: font-weight("medium");
  color: css-var("text-heading");
}

.#{$so-prefix}search-item-row-sku {
  font-size: font-size("xs");
  color: css-var("text-secondary");
}

.#{$so-prefix}search-item-row-value {
  font-size: font-size("sm");
  color: css-var("text-primary");
  text-align: right;

  &.in-stock {
    color: css-var("accent-success");
  }
  &.low-stock {
    color: css-var("accent-warning");
  }
  &.out-of-stock {
    color: css-var("accent-danger");
  }
}

// ============================================
// LEGACY SEARCH RESULTS
// ============================================

.#{$so-prefix}search-overlay-section {
  padding: spacing("2") 0;
}

.#{$so-prefix}search-overlay-section-title {
  padding: spacing("2") spacing("6");
  font-size: 11px;
  font-weight: font-weight("semibold");
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: css-var("text-muted");
}

.#{$so-prefix}search-overlay-results {
  padding: 0;
}

.#{$so-prefix}search-overlay-item {
  display: flex;
  align-items: center;
  gap: spacing("3");
  padding: spacing("3") spacing("6");
  text-decoration: none;
  color: inherit;
  transition: background $so-transition-base;

  &:hover,
  &.#{$so-prefix}focused {
    background: css-var("card-hover-bg");
    text-decoration: none;
  }
}

.#{$so-prefix}search-overlay-item-icon {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: css-var("card-hover-bg");
  border-radius: radius("lg");
  color: css-var("text-secondary");

  // Color variants
  &.blue {
    background: #e8f0fe;
    color: $so-primary;
  }
  &.green {
    background: #e6f4ea;
    color: $so-success;
  }
  &.orange {
    background: #fef7e0;
    color: $so-warning;
  }
  &.red {
    background: #fce8e6;
    color: $so-danger;
  }

  @include dark-mode {
    &.blue {
      background: rgba(115, 103, 240, 0.15);
      color: css-var("accent-primary");
    }
    &.green {
      background: rgba(40, 199, 111, 0.15);
      color: css-var("accent-success");
    }
    &.orange {
      background: rgba(255, 159, 67, 0.15);
      color: css-var("accent-warning");
    }
    &.red {
      background: rgba(234, 84, 85, 0.15);
      color: css-var("accent-danger");
    }
  }

  .material-icons {
    font-size: 20px;
  }
}

.#{$so-prefix}search-overlay-item-content {
  flex: 1;
}

.#{$so-prefix}search-overlay-item-title {
  font-size: font-size("base");
  font-weight: font-weight("medium");
  color: css-var("text-heading");
}

.#{$so-prefix}search-overlay-item-path {
  font-size: font-size("xs");
  color: css-var("text-secondary");
  margin-top: 2px;
}

.#{$so-prefix}search-overlay-item-arrow {
  font-size: 18px;
  color: css-var("text-muted");
  opacity: 0;
  transition: opacity $so-transition-base;

  .#{$so-prefix}search-overlay-item:hover &,
  .#{$so-prefix}search-overlay-item.#{$so-prefix}focused & {
    opacity: 1;
  }
}

// ============================================
// ACCOUNT CARDS
// ============================================

.#{$so-prefix}search-account-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: spacing("4");
  padding: spacing("4") spacing("6");
}

.#{$so-prefix}search-account-card {
  background: css-var("card-bg");
  border: 1px solid css-var("border-color");
  border-radius: radius("lg") + 2px;
  padding: spacing("4");
  cursor: pointer;
  transition: all $so-transition-base;
  display: flex;
  flex-direction: column;
  gap: spacing("3");

  &:hover {
    border-color: css-var("accent-primary");
    box-shadow: css-var("shadow");
  }
}

.#{$so-prefix}search-account-card-header {
  display: flex;
  align-items: center;
  gap: spacing("3");
}

.#{$so-prefix}search-account-card-icon {
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: radius("lg") + 2px;
  flex-shrink: 0;

  // Color variants
  &.blue {
    background: #e8f0fe;
    color: $so-primary;
  }
  &.green {
    background: #e6f4ea;
    color: $so-success;
  }
  &.orange {
    background: #fef7e0;
    color: $so-warning;
  }
  &.red {
    background: #fce8e6;
    color: $so-danger;
  }
  &.purple {
    background: #f3e8fd;
    color: #9334ea;
  }

  @include dark-mode {
    &.blue {
      background: rgba(115, 103, 240, 0.15);
      color: css-var("accent-primary");
    }
    &.green {
      background: rgba(40, 199, 111, 0.15);
      color: css-var("accent-success");
    }
    &.orange {
      background: rgba(255, 159, 67, 0.15);
      color: css-var("accent-warning");
    }
    &.red {
      background: rgba(234, 84, 85, 0.15);
      color: css-var("accent-danger");
    }
    &.purple {
      background: rgba(147, 51, 234, 0.15);
      color: #a855f7;
    }
  }

  .material-icons {
    font-size: 22px;
  }
}

.#{$so-prefix}search-account-card-info {
  flex: 1;
  min-width: 0;
}

.#{$so-prefix}search-account-card-name {
  font-size: font-size("base");
  font-weight: font-weight("semibold");
  color: css-var("text-heading");
  margin-bottom: 2px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.#{$so-prefix}search-account-card-category {
  font-size: 11px;
  font-weight: font-weight("medium");
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: css-var("text-muted");
}

.#{$so-prefix}search-account-card-balance {
  font-size: font-size("lg");
  font-weight: font-weight("semibold");
  text-align: right;

  &.positive {
    color: css-var("accent-success");
  }
  &.negative {
    color: css-var("accent-danger");
  }
  &.neutral {
    color: css-var("text-primary");
  }
}

.#{$so-prefix}search-account-card-details {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: spacing("2");
  padding-top: spacing("3");
  border-top: 1px solid css-var("border-color");
}

.#{$so-prefix}search-account-card-detail {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.#{$so-prefix}search-account-card-detail-label {
  font-size: 10px;
  font-weight: font-weight("medium");
  text-transform: uppercase;
  letter-spacing: 0.3px;
  color: css-var("text-muted");
}

.#{$so-prefix}search-account-card-detail-value {
  font-size: font-size("xs");
  font-weight: font-weight("medium");
  color: css-var("text-primary");
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

// Account Cards Section
.#{$so-prefix}search-account-section {
  padding: 0;
}

.#{$so-prefix}search-account-section-title {
  padding: spacing("4") spacing("6") spacing("2");
  font-size: font-size("xs");
  font-weight: font-weight("semibold");
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: css-var("text-muted");
}

// ============================================
// EMPTY STATE
// ============================================

.#{$so-prefix}search-empty {
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: spacing("12") + 12px spacing("6");
  text-align: center;

  &.#{$so-prefix}visible {
    display: flex;
  }
}

.#{$so-prefix}search-empty-icon {
  font-size: 64px;
  color: css-var("text-muted");
  margin-bottom: spacing("4");
}

.#{$so-prefix}search-empty-title {
  font-size: font-size("lg");
  font-weight: font-weight("medium");
  color: css-var("text-heading");
  margin-bottom: spacing("2");
}

.#{$so-prefix}search-empty-text {
  font-size: font-size("base");
  color: css-var("text-secondary");
}

// ============================================
// LOADING STATE
// ============================================

.#{$so-prefix}search-loading {
  display: none;
  align-items: center;
  justify-content: center;
  padding: spacing("10") spacing("6");

  &.#{$so-prefix}visible {
    display: flex;
  }
}

.#{$so-prefix}search-loading-spinner {
  width: 32px;
  height: 32px;
  border: 3px solid css-var("border-color");
  border-top-color: css-var("accent-primary");
  border-radius: radius("full");
  animation: so-search-spin 0.8s linear infinite;
}

@keyframes so-search-spin {
  to {
    transform: rotate(360deg);
  }
}

// ============================================
// HIGHLIGHT MATCH
// ============================================

.#{$so-prefix}search-overlay-item-title mark,
.#{$so-prefix}search-item-card-title mark,
.#{$so-prefix}search-item-row-title mark,
.#{$so-prefix}search-account-card-name mark {
  background: rgba(115, 103, 240, 0.2);
  color: css-var("accent-primary");
  padding: 0 2px;
  border-radius: 2px;

  @include dark-mode {
    background: rgba(115, 103, 240, 0.3);
  }
}

// ============================================
// RESPONSIVE
// ============================================

@include mobile {
  .#{$so-prefix}search-quick-links-grid {
    grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
  }

  .#{$so-prefix}search-results-grid {
    grid-template-columns: 1fr;
  }

  .#{$so-prefix}search-item-row {
    grid-template-columns: 1fr;
    gap: spacing("2");
  }

  .#{$so-prefix}search-item-row-value {
    text-align: left;
  }

  .#{$so-prefix}search-list-header {
    display: none;
  }

  .#{$so-prefix}search-filter-bar {
    flex-wrap: wrap;
  }

  .#{$so-prefix}search-overlay-footer {
    display: none;
  }

  .#{$so-prefix}search-overlay-header {
    padding: spacing("4");
  }

  .#{$so-prefix}search-overlay-input {
    font-size: font-size("lg");
  }

  .#{$so-prefix}search-account-cards {
    grid-template-columns: 1fr;
    padding: spacing("3") spacing("4");
  }
}

